import React, { useState, useEffect } from 'react';
import '../instructions.css'; 
import AudioPlayer from './TestAudio'; // Replace this with the actual component
import Cookies from 'js-cookie';
import logo from '../images/GCC-TBC.png';

const Instructions = () => {
  const [isChecked, setIsChecked] = useState(false);
  const [showNextComponent, setShowNextComponent] = useState(false);
  const [examInfo, setExamInfo] = useState();


  
  useEffect(() => {
    const UserId =  Cookies.get('user_id'); // replace 'your_user_id' with the actual UserId
    fetch(`http://13.126.253.132:5000/api/intro1/${UserId}`)
      .then(response => response.json())
      .then(data => {
        setExamInfo(data);
        if (data === 'yes') {
          setShowNextComponent(true);
        }
      })
      .catch(error => console.error('Error:', error));
    }, []);
      

  const handleProceed = async () => {
    if (isChecked) {
      setShowNextComponent(true);
      try {
        const UserId =  Cookies.get('user_id');
        const now = new Date();
        const dateTime = now.toLocaleString(); // Convert to local date/time string
        const logInfo = {
          user_id: UserId,
          information: ` ${dateTime}`
        };
        await fetch(`http://13.126.253.132:5000/api/introlog`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(logInfo)
        });
       
      } catch (error) {
        console.error('Error:', error);
      }
      
    } else {
      alert('Please confirm that the information is correct.');
    }
  };

  return (
    <div>
      {showNextComponent ? (
        <AudioPlayer />
      ) : (
        <div>
          <div className="title-bar">
            <div className="info-title">
              <img src={logo} alt="GCC-TBC" />
            </div>
            <h1>MSCE Pune Online Exam</h1>
          </div>
          <div className="container">
            <div className="information">Instructions</div>
            <div className="info-container">
              <div className="info-left">
              <p>Google Chrome चा वापर करावा.
                  मोबाईल android असावा.
                  परीक्षा सुरु झाल्यावर पुन्हा थांबवता येणार नाही.
                  स्किन लॉक सेटिंग किमान १० मिनिटे करावे.
                  परीक्षा सुरु असताना स्किन बंद होणार नाही, रिफ्रेश होणार नाही, क्लोज होणार नाही browser बंद होणार नाही याची काळजी घ्यावी.
                  परीक्षा संपल्यावर लॉगआउट करावे.
                  एकदा परीक्षा झालेला लॉगिन पुन्हा वापरु नये
                </p>
              </div>
            </div>
            <div className="checkbox-container">
              <input
                type="checkbox"
                id="info-check"
                checked={isChecked}
                onChange={() => setIsChecked(!isChecked)}
              />
              <label htmlFor="info-check">I have read and understood above instructions. <br />वरील सूचना मी वाचून समजून घेतल्या आहेत</label>
            </div>
            <div className="button-container">
              <button className="proceed-button" onClick={handleProceed}>
                Proceed
              </button>
            </div>
          </div>
          <div className="copyright">&copy; 2023</div>
        </div>
      )}
    </div>
  );
};

export default Instructions;